@import url(https://fonts.googleapis.com/css?family=Reenie+Beanie&display=swap);
body, html, #root {
    width: 100%;
    
    margin: 0;
    padding: 0;
}

html, #root {
    overflow-x: hidden;

    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    letter-spacing: 2px;
    color: #333;
}

.text-unselectable {
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
    
    -webkit-user-select: none;  
    -moz-user-select: none;    
    -ms-user-select: none;      
    user-select: none;
}

.clickable {
    cursor: pointer;
}

clickable:focus {
    outline:none;
}

a {
    text-decoration: none;
    -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 

    -webkit-animation: color-change 10000ms infinite;
    animation: color-change 10000ms infinite;
}

@-webkit-keyframes color-change {
    0% { color: #2196F3; }
    25% { color: #4CAF50; }
    50% { color: #FFC107; }
    75% { color: #FF5722; }
    100% { color: #2196F3; }
}

@keyframes color-change {
    0% { color: #2196F3; }
    25% { color: #4CAF50; }
    50% { color: #FFC107; }
    75% { color: #FF5722; }
    100% { color: #2196F3; }
}
/* desktop-x-small */
@media all and (min-width: 0) and (max-width: 480px) {
    .navbar-dimensions {
        height: 56px;
        width: 100%;
    }

    .navbar-item-dimensions {
        margin: 4px;
        padding-top: 16px;
        padding-bottom: 16px;
        padding-left: 8px;
        padding-right: 8px;
    }

    .navbar-logo {
        display: none;
    }
}

/* desktop-small */
@media all and (min-width: 481px) and (max-width: 840px) {
    .navbar-dimensions {
        height: 56px;
        width: 100%;
    }

    .navbar-item-dimensions {
        margin: 8px;
        padding-top: 16px;
        padding-bottom: 16px;
        padding-left: 8px;
        padding-right: 8px;
    }

    .navbar-logo {
        margin: 20px;
        padding: 16px;
    
        font-size: 18px;
    
        color: #333;
        border: 2px dashed #f69c55;
    }
}

/* desktop-medium */
@media all and (min-width: 841px) {
    .navbar-dimensions {
        height: 88px;
        width: 100%;
    }

    .navbar-item-dimensions {
        margin: 8px;
        padding: 16px;
    }

    .navbar-logo {
        margin: 20px;
        padding: 16px;
    
        font-size: 18px;
    
        color: #333;
        border: 2px dashed #f69c55;
    }
}

.navbar {
    position: fixed;

    z-index: 100;

    display: flex;
    justify-content: center; /* align horizontal */
    align-items: center; /* align vertical */

    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 700;
    line-height: 1em;
    color: #333;

    -webkit-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out;
}

.elevated {
    background-color: white;
    box-shadow: 0 3px 6px rgba(0,0,0,0.10);
}

.navbar-item {
    border-radius: 4px;

    -webkit-transition: all 200ms ease-in-out;
    transition: all 200ms ease-in-out;
}

.navbar-item:hover, .navbar-logo:hover {
    -webkit-transition: all 200ms ease-in;
    transition: all 200ms ease-in;

    background-color: rgba(0, 0, 0, 0.05);
}

.navbar-item-selected {
    border-radius: 4px;

    -webkit-animation: navbar-item-background-change 10000ms infinite;
    animation: navbar-item-background-change 10000ms infinite;
}

@-webkit-keyframes navbar-item-background-change {
    0% { background-color: rgba(33, 150, 243, 0.2); }
    25% { background-color: rgba(76, 175, 80, 0.2); }
    50% { background-color: rgba(255, 193, 7, 0.2); }
    75% { background-color: rgba(255, 87, 34, 0.2); }
    100% { background-color: rgba(33, 150, 243, 0.2); }
}

@keyframes navbar-item-background-change {
    0% { background-color: rgba(33, 150, 243, 0.2); }
    25% { background-color: rgba(76, 175, 80, 0.2); }
    50% { background-color: rgba(255, 193, 7, 0.2); }
    75% { background-color: rgba(255, 87, 34, 0.2); }
    100% { background-color: rgba(33, 150, 243, 0.2); }
}
.root-home {
    height: 100%;
    width: 100%;

    overflow-y: auto;
    overflow-x: hidden;

    margin: 0;
    padding: 0;
}

.work {
    display: flex;
    align-items: center;
    justify-content: center;
}

.route-fade-enter {
    opacity: 0;
}

.route-fade-enter.route-fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 600ms ease-in-out;
  transition: opacity 600ms ease-in-out;
}

.route-fade-exit {
    opacity: 1;
    position: fixed;
    display: none
}

.route-fade-exit.route-fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 0ms ease-in-out;
  transition: opacity 0ms ease-in-out;
}
